.scrolldown{
	width: 29px;
	height: 16px;
	position: absolute;
	z-index: 20;
	left: 50%;
	bottom: 40px;
	margin-left: -15px;
	background: url("/static/img/scrolldown.svg") no-repeat;
	-webkit-animation-name: animarScrollDown;
	        animation-name: animarScrollDown;
	-webkit-animation-iteration-count: infinite;
	        animation-iteration-count: infinite;
	-webkit-animation-duration: 3s;
	        animation-duration: 3s;
}
@-webkit-keyframes animarScrollDown{
0%{
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
}
50%{
		-webkit-transform: translateY(20px);
		        transform: translateY(20px);
}
100%{
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
}
}
@keyframes animarScrollDown{
0%{
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
}
50%{
		-webkit-transform: translateY(20px);
		        transform: translateY(20px);
}
100%{
		-webkit-transform: translateY(0px);
		        transform: translateY(0px);
}
}
